.memory {
  display: flex;
  flex-wrap: wrap;
  width: 40em;
  margin: auto;
  user-select: none;
}

.memory > .guesses {
  font-size: 1em;
  width: calc(100% - 0.4em);
  margin: 0.2em;
  text-align: center;
  font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
}

.memory > .card {
  font-size: 3.5em;
  flex: 1 1 calc(100% / 7 - 0.4em);
  outline: 0.08em solid silver;
  margin: 0.2em;
  display: flex;
  cursor: default;
}

.memory > .card.hidden {
  background: silver;
}

.memory > .card.justMatched,
.memory > .card.justMismatched {
  outline: 0.1em solid green;
  cursor: not-allowed;
}
.memory > .card.justMismatched {
  outline-color: red;
  cursor: not-allowed;
}

.memory > .card.visible {
  cursor: not-allowed;
}

.memory > .card > .symbol {
  margin: auto;
}